@import url(https://fonts.googleapis.com/css?family=DM+Serif+Display|Thasadith:400,700&display=swap);
body {
  margin: 0;
  font-family: "DM Serif Display", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  /* padding: 1rem; */
  text-align: center;
  color: #283033;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 100;
}

.gradient-backg {
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#86c6e7),
    color-stop(#7cbee1),
    color-stop(#72b7da),
    color-stop(#68afd4),
    color-stop(#5ea8ce),
    color-stop(#57a1c8),
    color-stop(#4f9ac2),
    color-stop(#4893bc),
    color-stop(#428bb4),
    color-stop(#3c83ac),
    color-stop(#377ba5),
    to(#31739d)
  );
  background-image: -webkit-linear-gradient(
    top,
    #86c6e7,
    #7cbee1,
    #72b7da,
    #68afd4,
    #5ea8ce,
    #57a1c8,
    #4f9ac2,
    #4893bc,
    #428bb4,
    #3c83ac,
    #377ba5,
    #31739d
  );
  background-image: linear-gradient(
    to bottom,
    #86c6e7,
    #7cbee1,
    #72b7da,
    #68afd4,
    #5ea8ce,
    #57a1c8,
    #4f9ac2,
    #4893bc,
    #428bb4,
    #3c83ac,
    #377ba5,
    #31739d
  );
}

